import "./styles.css";
import moment from "moment";

export default function App() {
  const startDate = moment().startOf("week");
  const endDate = moment().add(1, "month");
  const arrangementStart = {
    date: moment("2022-12-26"),
    peopleIndex: 0
  };
  const people = ["Henry", "Ken", "Leo", "Yogurt", "Nana"];
  const totalViews = [];
  let weekViews = [];
  while (startDate.isBefore(moment(endDate))) {
    const currentDate = startDate.add(1, "days");
    const today = {
      date: currentDate.format("YYYY-MM-DD"),
      dayISO: currentDate.format("ddd"),
      day: currentDate.day(),
      people: people[currentDate.diff(arrangementStart.date, "day") % 5]
    };
    weekViews.push(today);
    if (today.day === 0) {
      totalViews.push(weekViews);
      weekViews = [];
    }
  }

  return (
    <div className="App">
      <table>
        <tbody>
          {totalViews.map((week) => (
            <tr>
              {week.map((day) => (
                <td
                  className={
                    moment(day.date).isSame(moment(), "day") ? "active" : ""
                  }
                >
                  <div className="day">
                    <div className="header">
                      {day.date}
                      <br />
                      {day.dayISO}
                    </div>
                    <div className="people">
                      <b>{day.people}</b>
                    </div>
                  </div>
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
